import { Action } from '@ngrx/store';
import *  as rootState from '../../../reducers/index'
import { CommonReportActionTypes } from './common-report.actions';


export const commonReportFeatureKey = 'commonReport';

export interface ReportData {
 initialData : any
}

export const initialState: ReportData = {
  initialData:{}
};

export function reducer(state = initialState, action ): ReportData {
  switch (action.type) {
    case CommonReportActionTypes.LoadMaterialAnalysis :
      return Object.assign({},state,{initialData:action.payload})
    default:
      return state;
  }
}

export interface State extends rootState.State {
'CommonReportData' : ReportData

} 