import { ReportviewerComponent } from './components/reportviewer/reportviewer.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from 'src/app/core/guards/auth.guard';
import { CommonReportComponent } from './commonreport.component';
import { GenericReportComponent } from './components/generic-report/generic-report.component';
import { SalesAnalysisReportComponent } from './components/commonsales-report/sales-analysis-report/sales-analysis-report.component';
import { MaterialAnalysisReportComponent } from './components/commonsales-report/material-analysis-report/material-analysis-report.component';
import { CertificateAnalysisComponent } from './components/commonsales-report/certificate-analysis/certificate-analysis.component';
import { CustomreportsComponent } from './components/customreports/customreports.component';
import { ItemMasterReportComponent } from './components/inventory-master-reports/item-master-report/item-master-report.component';
import { TransactionActivityReportComponent } from './components/inventory-transaction-reports/transaction-activity-report/transaction-activity-report.component';
import { AgingReportComponent } from './components/inventory-transaction-reports/aging-report/aging-report.component';
import { ValuationReportComponent } from './components/inventory-transaction-reports/valuation-report/valuation-report.component';
import { CompanyInformationReportComponent } from './components/company-information-report/company-information.component';
import { TransactionHistoryReportComponent } from './components/inventory-transaction-reports/transaction-history-report/transaction-history-report.component';
import { LotExpirationComponent } from './components/serial-lot-reports/lot-expiration-report/lot-expiration-report.component';
import { MaterialPropertyMasterReportComponent } from './components/inventory-master-reports/material-property-master-report/material-property-master-report.component';
// import { DrawSampleQcReportComponent } from './components/quality-control-reports/draw-sample-qc/draw-sample-qc.component';
import { ReportgridviewlistComponent } from './components/reportgridviewlist/reportgridviewlist.component';
import {PhysicalPropertyEquationReportComponent} from './components/physical-property-equation-report/physical-property-equation-report.component';
import { AuditHistoryDashboardComponent } from './components/audit-history-dashboard/audit-history-dashboard.component';

const routes: Routes = [
    {
        path: '',
        component: CommonReportComponent,
        children: [
            { path: '', redirectTo: 'report', pathMatch: 'full' },
            { path: 'MaterialAnalysis', component: MaterialAnalysisReportComponent, canActivate: [AuthGuard] },
            { path: 'SalesAnalysis', component: SalesAnalysisReportComponent, canActivate: [AuthGuard] },
            { path: 'CertificateAnalysis', component: CertificateAnalysisComponent, canActivate: [AuthGuard] },
            { path: 'reportviewer', component: ReportviewerComponent, canActivate: [AuthGuard] },
            { path: 'report/:name', component: GenericReportComponent, canActivate: [AuthGuard] },
            { path: 'customreport/cusrep/:name', component: CustomreportsComponent, canActivate: [AuthGuard] },
            { path: 'inventory-master-reports/item-master-report', component: ItemMasterReportComponent, canActivate: [AuthGuard] },
            { path: 'inventory-transaction-reports/transaction-activity-report', component: TransactionActivityReportComponent, canActivate: [AuthGuard] },
            { path: 'inventory-transaction-reports/inventory-aging-report', component: AgingReportComponent, canActivate: [AuthGuard] },
            { path: 'inventory-transaction-reports/valuation-report', component: ValuationReportComponent, canActivate: [AuthGuard] },
          { path: 'company-information-report', component: CompanyInformationReportComponent, canActivate: [AuthGuard] },
          { path: 'inventory-transaction-reports/transaction-history-report', component: TransactionHistoryReportComponent, canActivate: [AuthGuard] },
          { path: 'serial-lot-reports/lot-expiration-report', component: LotExpirationComponent, canActivate: [AuthGuard] },
          { path: 'inventory-master-reports/material-property-master-report', component: MaterialPropertyMasterReportComponent, canActivate: [AuthGuard] },
          { path: 'reportGridViewList', component: ReportgridviewlistComponent, canActivate: [AuthGuard] },
          {path:'physicalProperty-equation-report', component:PhysicalPropertyEquationReportComponent, canActivate:[AuthGuard]},
          {path:'audithistorydashboard', component:AuditHistoryDashboardComponent, canActivate:[AuthGuard]},
         // {path: 'quality-control-reports/draw-sample-qc', component: DrawSampleQcReportComponent,canActivate:[AuthGuard]}
        ]
    }
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class CommonReportRouting {

}


