import { Component, OnInit } from '@angular/core';
import { AppUtility } from 'src/app/core/services/utility.services';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-material-property-master-report',
  templateUrl: './material-property-master-report.component.html',
  styleUrls: ['./material-property-master-report.component.scss']
})
export class MaterialPropertyMasterReportComponent implements OnInit {

  public screenName = 'Material Property Master Report'
  constructor(private utilityService: AppUtility, private router: Router) {

  }

  ngOnInit() {
    this.process();
    this.router.navigate(['/main/dashboard/dashboardpage']);
  }

  process() {

    let jObject = {
      ReportName: this.screenName,

      ListingFlag: false,
      ListingVariable: ''
    };
    this.utilityService.bmgfReportAction(jObject,this.screenName).then(res =>{
    })
  }

}
